<template>
  <v-app id="inspire">
    <v-main class="blue-grey lighten-1">
      <v-container style="max-width: 450px" fill-height>
        <v-layout align-center row wrap>
          <v-flex xs12>
            <v-card
            :loading ="loading">
              <div class="pa-5">
                <h1 style="text-align: center" class="mb-5">Login</h1>
                <form>
                  <v-text-field
                    label ="ID"
                    v-model="uid"
                    prepend-inner-icon="mdi-account">
                    </v-text-field>
                  <v-text-field
                    prepend-inner-icon="mdi-lock"
                    type="password"
                    label="password"
                    v-model="password"
                  >
                  </v-text-field>
                    <v-btn
                      type="submit"
                      color="blue lighten-2 text-capitalize"
                      depressed
                      large
                      block
                      dark
                      class="mb-3"
                      @click="login"
                      >
                      Sign in
                    </v-btn>
                    <v-btn
                        type="submit"
                        color="red lighten-3 text-capitalize"
                        depressed
                        large
                        block
                        dark
                        class="mb-3"
                        router-link to ="/signup"
                    >Sign up

                    </v-btn>
                </form>
                <v-snackbar
                    dense outlined
                    dismissible
                    type="warning"
                    v-model="httpstatus.show"
                    timeout="3000"
                    color="red accent-2"

                >
                  {{httpstatus.message}}
                </v-snackbar>

              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>

//const storage = window.sessionStorage
//import VueCookies from 'vue-cookies'


export default {
  data: () => {
    return{
      uid : null,
      password : null,
      loading : false,
      httpstatus: {
        show:false,
        message: null,
        color : "red accent-2",
      },
      token: "",
      info: ""
    }
  },
  methods: {
    async login(e){

      e.preventDefault();
      await this.$http.post("/auth/signin", {
        uid: this.uid,
        password: this.password,
      })
          .then(res => {
            // event.defaultPrevented();
            console.log(res.status);
            this.loading = true
            setTimeout(()=> (this.loading = false),1000)
            this.$router.push({name: "Home"})

          })
          .catch(e => {
            console.log("error" , JSON.stringify(e.response));
            let status = e.response.status;
            if (status === 4001){
                  this.httpstatus = {
                    message: "아이디가 존재하지 않습니다.",
                    show : true,
              }
            }else if(status === 4002){
              this.httpstatus = {
                message : "비밀번호 불일치",
                show : true,
              }
            }else if(status === 4003){
              this.httpstatus = {
                message : "비밀번호를 입력해주세요.",
                show : true,
              }
            }
            //this.setInfo("fail","",JSON.stringify(e.response || e.message));
          })
          .finally(e => {
            console.log("finall", e);
          });

    },
  },
  mounted() {
  }

}

</script>